@-webkit-keyframes shine {
  0%, 100%{
    -webkit-transform: scale(.3);
            transform: scale(.3);
    opacity: .2;
  }
  50% {
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
    opacity: 1;
  }
}
@keyframes shine {
  0%, 100%{
    -webkit-transform: scale(.3);
            transform: scale(.3);
    opacity: .2;
  }
  50% {
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
    opacity: 1;
  }
}

@-webkit-keyframes rotate {
  0%{
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
@keyframes rotate {
  0%{
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@-webkit-keyframes move {
  0%{
    -webkit-transform:     translate(-100px, 22px);
            transform:     translate(-100px, 22px);
  }
  100% {
    -webkit-transform:     translate(100px, -22px);
            transform:     translate(100px, -22px);
  }
}
@keyframes move {
  0%{
    -webkit-transform:     translate(-100px, 22px);
            transform:     translate(-100px, 22px);
  }
  100% {
    -webkit-transform:     translate(100px, -22px);
            transform:     translate(100px, -22px);
  }
}

.app {
  background: #18313d;
  position: relative;
}

.card{
  position: absolute;
  width: 80%;
  height: 80%;
  top:10%;
  left:10%;
  background: #1b3d4d;
  -webkit-box-shadow: 0 0 2rem 1rem #152c36;
          box-shadow: 0 0 2rem 1rem #152c36;
}

.star{
  background:#f4e3da;
  position: absolute;
}
.star:before,
.star:after{
  content: '';
  width: 200%;
  height: 20%;
  border-radius:100%;
  position: absolute;
  top:40%;
  left:-50%;
  background-image:-webkit-gradient(linear,left top, right top,from(#fff), color-stop(30%, #f4e3da), color-stop(70%, #f4e3da), to(#fff));
  background-image:-webkit-linear-gradient(left,#fff, #f4e3da 30%, #f4e3da 70%, #fff);
  background-image:-o-linear-gradient(left,#fff, #f4e3da 30%, #f4e3da 70%, #fff);
  background-image:linear-gradient(90deg,#fff, #f4e3da 30%, #f4e3da 70%, #fff);
  -webkit-transform-origin: center center;
      -ms-transform-origin: center center;
          transform-origin: center center;
}
.star:before{
  -webkit-transform: rotate(45deg);
      -ms-transform: rotate(45deg);
          transform: rotate(45deg);
}
.star:after{
  -webkit-transform: rotate(-45deg);
      -ms-transform: rotate(-45deg);
          transform: rotate(-45deg);
}



.moon{
  width: 15rem;
  height: 15rem;
  border-radius: 50%;
  background: #415f71;
  position: fixed;
  top:10%;
  left:50%;
  -webkit-transform: translateX(-50%);
      -ms-transform: translateX(-50%);
          transform: translateX(-50%);
}

.ring{
  width: 96%;
  height: 96%;
  border-radius:50%;
  position: absolute;
  top:1.5%;
  left:1.5%;
  border: 1px solid #4e6e81;
}
.birght{
  width: 94%;
  height: 94%;
  border-radius:50%;
  position: absolute;
  top:3%;
  left:3%;
  /* background: #f4e3da; */
  /* background-image:radial-gradient(circle at 90% 10%,#f4e3da 0, #f4e3da 60%, #cab8b0 60%, #cab8b0 100%); */
}

.shine {
	-webkit-animation: shine 2s cubic-bezier(0.250, 0.460, 0.750, 0.940) forwards infinite;
	        animation: shine 2s cubic-bezier(0.250, 0.460, 0.750, 0.940) forwards infinite;
}


.car{
  width: 27rem;
  height: 9rem;
  position: absolute;
  top:50%;
  left: 20%;
  
  -webkit-animation: move 10s linear forwards;
  
          animation: move 10s linear forwards;
}
.car img{
  position: absolute;
  width: 100%;
  height: 100%;
}

#car-front-wheel{
  -webkit-animation: rotate 4s linear forwards infinite;
  animation: rotate 4s linear forwards infinite;
  -webkit-transform-origin: 72.86% 52.35%;
      -ms-transform-origin: 72.86% 52.35%;
          transform-origin: 72.86% 52.35%;
}

#car-back-wheel{
  -webkit-animation: rotate 4s linear forwards infinite;
  animation: rotate 4s linear forwards infinite;
  -webkit-transform-origin: 16% 75.5%;
      -ms-transform-origin: 16% 75.5%;
          transform-origin: 16% 75.5%;
}


